@import "./../../../styles/constants.scss";

.header {
  position: absolute;
  z-index: 5;
  min-width: 100%;
  padding: 20px;
  background-color: $opacity-background;
  @media (max-width: 1200px) {
    padding: 15px;
  }
  @media (max-width: 992px) {
    padding: 10px;
  }
  @media (max-width: 768px) {
    padding: 5px;
  }
  @media (max-width: 415px) {
    padding: 10px;
  }
  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
